var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-sm-12"},[_c('h1',[_vm._v("The World of Altasoror")]),_c('b-tabs',[_c('b-tab',{attrs:{"title":"Intro"}},[_c('div',[_c('p',[_vm._v("\n          Welcome to the world of Altasoror, a world created for the Between Worlds campaign.\n        ")]),_c('p',[_vm._v("\n          It all began with the prophet. Thousands of years ago, the prophet Sorore (Sore - Or) walked upon the planet and taught the world and its people all there was to know. The prophet taught the world of the one true god, the great creator, who guides us all towards prosperity. The prophet traveled to all parts of the world and spread the Creators message. The prophet spoke of peace, of rules, of cooperation, of humanity itself. The prophet created the Tenants of Order which all should follow. The prophet also knew how to build society, from its government to its cities, to its farmland. The Creator gifted the prophet will all knowledge. Once the world was taught, the prophet left leaving only one thing behind.\n        ")]),_c('p',[_vm._v("\n          In the city of Sornal, in the kingdom of Sesta, where the prophet was born. The prophet left behind a stone tablet, inscribed with dire warning of doom, and the world's potential salvation. A hero with silver hair will stop the world from falling to this darkness. Unfortunately, the exact wording of the warning is unknown, as the Sesta have betrayed the prophets will, and guard the tablet from those who would read it.\n        ")]),_c('p',[_vm._v("\n          Since then, the world has become more divided in its interpretation of the prophets tenants. Those born with silver hair are raised as potential saviors. Trained in sword and diplomacy, to protect against all possible threats which may destroy the world. But all have been false saviors, having died in their journey, or turning to corruption. The world continues waiting for the true hero, and the doom that comes with.\n        ")]),_c('p',[_vm._v("\n          Those who have tried to expand upon the worlds knowledge are met with a horrid fate. Those to delve into magic are consumed by it, either killed or driven mad. Those who attempt to advance technology meet with similar results. The world has not evolved much since the prophet left. Innovation is stagnant, witchcraft is forbidden in all parts of the world, and inventors are despised, or worse.\n        ")]),_c('p',[_vm._v("\n          But this world is mostly peaceful. No great evils threaten the world, and mythical beasts are just that, myth. Political discourse is mostly positive throughout the world, save for the barbaric Sesta, and the lawless lands of Astipan. It is exactly as the prophet foretold. Which makes the world even more cautious about the prophecy of doom that the Sesta are hiding.\n        ")])])]),_c('b-tab',{attrs:{"title":"Atlas"}},[_c('div',[_c('h3',[_vm._v("Map of Altasoror")]),_c('img',{attrs:{"src":"https://elthelas-images.herokuapp.com/maps/altasoror-map.jpg","alt":"map of altasoror"}}),_c('h3',[_vm._v("Kingdom Descriptions")]),_c('h3',[_vm._v("Estar")]),_c('strong',[_vm._v("Main Export:")]),_vm._v(" Labour, workers"),_c('br'),_c('strong',[_vm._v("Government:")]),_vm._v(" Monarchy"),_c('br'),_c('strong',[_vm._v("Current Leader:")]),_vm._v(" Jalen Raldrud"),_c('br'),_c('strong',[_vm._v("Allied Kingdoms:")]),_vm._v(" Ochar, Astipan, Malthein"),_c('br'),_c('strong',[_vm._v("Enemy Kingdoms:")]),_vm._v(" Sesta"),_c('br'),_c('strong',[_vm._v("Population:")]),_vm._v(" 4 Million"),_c('br'),_c('strong',[_vm._v("Religion:")]),_vm._v(" Sororism"),_c('br'),_c('strong',[_vm._v("Main City:")]),_vm._v(" Estapor"),_c('br'),_c('strong',[_vm._v("Iconic Item/Landmark:")]),_vm._v(" Sorores Blade. A sword that was supposedly made for Sorore, who supposedly carried it, but never used it."),_c('br'),_c('strong',[_vm._v("Description:")]),_vm._v(" Estar is a very traditional kingdom of a King, Queen, Lords, and knights. They most closely follow the tenants of order. Estar attempts to be the enforcers of the world, when you need help or a hero, you go to Estar. They share a border with the barbaric Sesta, and are often in conflicts with them.\n         \n        "),_c('h3',[_vm._v("Malthein")]),_c('strong',[_vm._v("Main Export:")]),_vm._v(" Salt, Minerals"),_c('br'),_c('strong',[_vm._v("Government:")]),_vm._v(" Elder council"),_c('br'),_c('strong',[_vm._v("Current Leader:")]),_vm._v(" Sasha Lao"),_c('br'),_c('strong',[_vm._v("Allied Kingdoms:")]),_vm._v(" Estar, Frugua, Ochar"),_c('br'),_c('strong',[_vm._v("Enemy Kingdoms:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Population:")]),_vm._v(" 300,000"),_c('br'),_c('strong',[_vm._v("Religion:")]),_vm._v(" Soroism"),_c('br'),_c('strong',[_vm._v("Main City:")]),_vm._v(" Titans Heart"),_c('br'),_c('strong',[_vm._v("Iconic Item/Landmark:")]),_vm._v(" Sorores Podium. A unique geological location in the mountains that create a massive echo effect to give grand speeches to the open area below."),_c('br'),_c('strong',[_vm._v("Description:")]),_vm._v(" The people of Malthein lead a more reclusive lifestyle. The live among and even inside the mountains of Malthein. The follow Soroism but only mildly, and place more emphasis on wisdom and decision making. They travel the mountains regularly on spiritual quests of enlightenment.\n     \n        "),_c('h3',[_vm._v("Sesta")]),_c('strong',[_vm._v("Main Export:")]),_vm._v(" Slaves"),_c('br'),_c('strong',[_vm._v("Government:")]),_vm._v(" Leader completes a trail set by the previous leader"),_c('br'),_c('strong',[_vm._v("Current Leader:")]),_vm._v(" Currently Undecided"),_c('br'),_c('strong',[_vm._v("Allied Kingdoms:")]),_vm._v(" Astipan, Ochar"),_c('br'),_c('strong',[_vm._v("Enemy Kingdoms:")]),_vm._v(" Frugua, Estar, Chechniar, Malthein"),_c('br'),_c('strong',[_vm._v("Population:")]),_vm._v(" 1 Million"),_c('br'),_c('strong',[_vm._v("Religion:")]),_vm._v(" Sornal (Named after the city)"),_c('br'),_c('strong',[_vm._v("Main City:")]),_vm._v(" Sornal, The Silent City"),_c('br'),_c('strong',[_vm._v("Iconic Item/Landmark:")]),_vm._v(" Sorores tablet, which contains the prophecy of doom. Hidden deep within the Silent City. Forbidden to all except the Sestas leader."),_c('br'),_c('strong',[_vm._v("Description:")]),_vm._v(" The Sesta are a strong and proud people. Valuing their ancestors wisdom above all. After the time of Sorore, their ancestors decided that Sorore was a prophet of doom and destruction. They rejected the beliefs of Sororism and creating their own religion called Sornal. It rejects all the tenants and does as much the opposite to oppose Sorore as possible. They enslave others, have destroyed most of Astipan, wage war with Estar, and take from Ochar."),_c('br'),_c('h3',[_vm._v("Frugua")]),_c('strong',[_vm._v("Main Export:")]),_vm._v(" Animal and Fur"),_c('br'),_c('strong',[_vm._v("Government:")]),_vm._v(" Democracy"),_c('br'),_c('strong',[_vm._v("Current Leader:")]),_vm._v(" Zoldim Chemtil"),_c('br'),_c('strong',[_vm._v("Allied Kingdoms:")]),_vm._v(" Malchein, Chechniar, Ochar"),_c('br'),_c('strong',[_vm._v("Enemy Kingdoms:")]),_vm._v(" Sesta"),_c('br'),_c('strong',[_vm._v("Population:")]),_vm._v(" 800,000"),_c('br'),_c('strong',[_vm._v("Religion:")]),_vm._v(" Soric"),_c('br'),_c('strong',[_vm._v("Main City:")]),_vm._v(" Ukalale"),_c('br'),_c('strong',[_vm._v("Iconic Item/Landmark:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Description:")]),_vm._v(" Frugua is basically a desert, as the mountain ranges of Malthein block most rains to the area. The people that live here are a close knit community, focusing a lot on family and cooperation. Though they follow the teachings of soric, they arent especially religious. They live in moving tent colonies to avoid the most extreme weather of the lands. Most travelers to Frugua tend to stay, as the feeling of community and cooperation cant truly be found elsewhere in the world. Trade is scarce, as they have little to give, and trips out into the desert are harsh for the unprepared. People of Frugua tend to be shorter, and more slender than other places in the world.\n    \n        "),_c('h3',[_vm._v("Astipan")]),_c('strong',[_vm._v("Main Export:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Government:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Current Leader:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Allied Kingdoms:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Enemy Kingdoms:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Population:")]),_vm._v(" 200,000"),_c('br'),_c('strong',[_vm._v("Religion:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Main City:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Iconic Item/Landmark:")]),_vm._v(" The Blood Tree. A one of a kind tree of unknown origin. The Sesta waged war with Astipan to destroy the tree, believing it to be from Soror, once they reached it they decided against destroying it."),_c('br'),_c('strong',[_vm._v("Description:")]),_vm._v(" Astipan has fallen to disarray since being destroyed by Sesta, there is no government, no set cities left. Bandits, Wizards, Inventors flee to Astipan to avoid oversight. The people mostly fled to Estar as well as Ochal and Checkniar.\n     \n        "),_c('h3',[_vm._v("Ochal")]),_c('strong',[_vm._v("Main Export:")]),_vm._v(" Food, grains, livestock"),_c('br'),_c('strong',[_vm._v("Government:")]),_vm._v(" Many local elected leaders manage individual townships"),_c('br'),_c('strong',[_vm._v("Current Leader:")]),_vm._v(" Several"),_c('br'),_c('strong',[_vm._v("Allied Kingdoms:")]),_vm._v(" All"),_c('br'),_c('strong',[_vm._v("Enemy Kingdoms:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Population:")]),_vm._v(" 350,000"),_c('br'),_c('strong',[_vm._v("Religion:")]),_vm._v(" Sororism"),_c('br'),_c('strong',[_vm._v("Main City:")]),_vm._v(" None"),_c('br'),_c('strong',[_vm._v("Iconic Item/Landmark:")]),_vm._v(" Dragons rest. A large hill that is out of place with the rest of the landscape."),_c('br'),_c('strong',[_vm._v("Description:")]),_vm._v(" Ochal is the main source of farmland throughout the region. The land has spotted small towns of trade centers, but they are few and far between. There is no set leader, other than minor town leaders. As the central country, all other countries must go here for extra food for the winter. Even though it has little in the way of defenses, it makes offerings to all other countries in order to keep the peace.\n     \n        "),_c('h3',[_vm._v("Chechniar")]),_c('strong',[_vm._v("Main Export:")]),_vm._v(" Luxury goods"),_c('br'),_c('strong',[_vm._v("Government:")]),_vm._v(" Theology"),_c('br'),_c('strong',[_vm._v("Current Leader:")]),_vm._v(" Sorom Enrico Gritti"),_c('br'),_c('strong',[_vm._v("Allied Kingdoms:")]),_vm._v(" Frugua, Ochar,"),_c('br'),_c('strong',[_vm._v("Enemy Kingdoms:")]),_vm._v(" Sesta"),_c('br'),_c('strong',[_vm._v("Population:")]),_vm._v(" 1.5 Million"),_c('br'),_c('strong',[_vm._v("Religion:")]),_vm._v(" Soric"),_c('br'),_c('strong',[_vm._v("Main City:")]),_vm._v(" Venier"),_c('br'),_c('strong',[_vm._v("Iconic Item/Landmark:")]),_vm._v(" Mantle of the Prophet. A scarf that the Sorom wears, it was supposedly worn by the prophet."),_c('br'),_c('strong',[_vm._v("Description:")]),_vm._v(" Chechniar is the kingdom of wealth and prosperity. A land rich in money and religion. It is the central land for the religion of Soric. A far more catholic style religion, featuring a pope-like figure In the Sorom, who claims to be the voice of the prophet. In reality, this church is mostly corrupted by greed and wealth. The practices that made the kingdom rich have been exploited to make the rich richer. And having a religious figure as their leader, who is then appointed by a council of other religious figures, very little looks like it will change for the better.\n      ")])]),_c('b-tab',{attrs:{"title":"Timelines"}},[_c('h3',[_vm._v("Available Time Portals")]),_c('h4',[_vm._v("Altasoror")]),_c('ul',[_c('li',[_vm._v("Year 0  ?")]),_c('li',[_vm._v("Year 1363 Day 122 - Current: Day 141  Take the Amulet of Justice")]),_c('li',[_vm._v("Year 1372 Day 322  Current Day 378  Take the Planer Gate/ Mantle of the prophet")]),_c('li',[_vm._v("Year 1387 Day 120  ?")]),_c('lip',[_vm._v("Year 1390 Day 175 - ?"),_c('li',[_vm._v("Year 1392 Day 1 End of Reality - Return the Sword of the Prophet")])])],1),_c('h4',[_vm._v("Elthelas")]),_c('ul',[_c('li',[_vm._v("Year -507 Maridia 23rd  Take the Book of Vile Darkness / Return ?")]),_c('li',[_vm._v("Year 1847 Sylvanus 2nd Take the Sword of the Prophet")]),_c('li',[_vm._v("Year 1858 Moltyr 13th - ?"),_c('p')]),_c('li',[_vm._v("Year 1868  Amatherin 30th  Return the Planer Gate")]),_c('li',[_vm._v("Year 2671  Return the Book of vile Darkness")]),_c('li',[_vm._v("Year 3605 -Return the Amulet of Justice")])]),_c('h3',[_vm._v("Times of Importance")]),_c('p',[_vm._v("Year 1367 AS Day 314 Hanzo is Exiled")]),_c('p',[_vm._v("Year 1369 AS Day 41  Astipan is destroyed")]),_c('p',[_vm._v("Year 1371 AS Day 386 Hanzo returns to Astipan and is killed")]),_c('p',[_vm._v("Year 1372 AS Day 222 Xane Ludor dies")]),_c('p',[_vm._v("Year 1846 YFC Coranus 27th  Martinus Cato Dies")]),_c('p',[_vm._v("Year 1869 YFC 3rd day of New Year - Ruthirs Dies")]),_c('p',[_vm._v("Year 1872 YFC 2nd day of New Year  Agriel Osiris Dies")])]),_c('b-tab',{attrs:{"title":"extrainfo"}},[_c('h3',[_vm._v("Altasoror Church Information")]),_c('p',[_vm._v("Nobody knows the prophet true name or appearance. Depictions only show a mysterious figure with the iconic mantle and the sword.")]),_c('p',[_vm._v("AltaSorors calendar is 400 days in a year, with no months. Each 100 days represents the change of seasons. 1-100 is spring season, 101-200 is summer season, 201-300 is fall season, 301-400 is winter season. Years are referenced in AS  After Soror.")]),_c('p',[_vm._v("Sorore  with an E, referenced the prophet directly. For all other uses, the E is dropped.")]),_c('p',[_vm._v("Potential Saviors  Those born with silver hair, are expected to visit all the counties of the world during their journey. If they are born in a place without the structure to train them, Malthein will always take them in. The silver hair of savior is quite distinct, and no hair dye or magic has been able to replicate its appearance. Anyone who has seen a true silver haired savoir would easily be able to tell the difference.")]),_c('h3',[_vm._v("Religions")]),_c('p',[_vm._v("There are only 3 religions in the world, described below.")]),_c('p',[_vm._v("Sororism  The most common religion in Altasoror. It is a general worship of the creator and following the prophets will. There are no required church meetings, tithes or rituals.")]),_c('p',[_vm._v("Soric -  A far more organized religion that Sororism. The soric church governs over Chechniar, and all its inhabitants are required to pay a monthly tithe to the church. There is a very set organizational hierarchy, with the Sorom being the religious and governmental leader of the country.")]),_c('p',[_vm._v("Sornal  Only followed by the Sesta. They believed the teachings of the prophet would lead to the doom of the world. As such, they do everything in their power to disregard and work against the prophets will and those that would follow them.")])]),_c('b-tab',{attrs:{"title":"NPCs"}},[_c('h2',[_vm._v("Allies")]),_c('h4',[_vm._v("Beladonis")]),_c('p',[_c('a',{attrs:{"href":"https://docs.google.com/document/d/1diJ_vKN03Esbddhd3ytSXHxNY-IBZqMhrv4-38MC-rU/edit"}},[_vm._v("Beladonis Magical Wonders")])]),_c('h4',[_vm._v("Elise")]),_c('h4',[_vm._v("Rutherford")]),_c('h4',[_vm._v("Cecilia")]),_c('h4',[_vm._v("Jimmy")]),_c('h4',[_vm._v("Pickles")]),_c('h2',[_vm._v("Enemies")]),_c('h4',[_vm._v("Sorom Enrico")]),_c('h4',[_vm._v("Lord Rem")]),_c('h4',[_vm._v("Paradox")]),_c('h2',[_vm._v("Others")])]),_c('b-tab',{attrs:{"title":"PCs"}},[_c('h3',[_vm._v("Ruthir")]),_c('p',[_c('strong',[_vm._v("Race:")]),_vm._v(" Firbolg")]),_c('p',[_c('strong',[_vm._v("Class/Level:")]),_vm._v(" Moon Druid 5")]),_c('p',[_c('router-link',{attrs:{"to":"/tools/charbuilder/5dd18c710e4d1e0004bf3534"}},[_vm._v("Character Sheet")])],1),_c('table',{staticClass:"table table-striped"},[_c('thead',[_c('tr',[_c('th',[_vm._v("STR")]),_c('th',[_vm._v("DEX")]),_c('th',[_vm._v("CON")]),_c('th',[_vm._v("INT")]),_c('th',[_vm._v("WIS")]),_c('th',[_vm._v("CHA")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("10")]),_c('td',[_vm._v("10")]),_c('td',[_vm._v("14")]),_c('td',[_vm._v("12")]),_c('td',[_vm._v("18")]),_c('td',[_vm._v("12")])])])]),_c('h3',[_vm._v("Hanzo Rito")]),_c('p',[_c('strong',[_vm._v("Race:")]),_vm._v(" Altasoror Human")]),_c('p',[_c('strong',[_vm._v("Class/Level:")]),_vm._v(" Eldritch Knight Fighter 5")]),_c('p',[_c('router-link',{attrs:{"to":"/tools/charbuilder/5d5c3e1bf1651e000430a212"}},[_vm._v("Character Sheet")])],1),_c('table',{staticClass:"table table-striped"},[_c('thead',[_c('tr',[_c('th',[_vm._v("STR")]),_c('th',[_vm._v("DEX")]),_c('th',[_vm._v("CON")]),_c('th',[_vm._v("INT")]),_c('th',[_vm._v("WIS")]),_c('th',[_vm._v("CHA")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("18")]),_c('td',[_vm._v("8")]),_c('td',[_vm._v("16")]),_c('td',[_vm._v("13")]),_c('td',[_vm._v("10")]),_c('td',[_vm._v("10")])])])]),_c('h3',[_vm._v("Agriel Osiris/Martinus Cato")]),_c('p',[_c('strong',[_vm._v("Race:")]),_vm._v(" Scourge Aasimar")]),_c('p',[_c('strong',[_vm._v("Class/Level:")]),_vm._v(" Hexblade Warlock 5")]),_c('p',[_c('router-link',{attrs:{"to":"/tools/charbuilder/5dbca6a3ae1e390004f9fa78"}},[_vm._v("Character Sheet")])],1),_c('table',{staticClass:"table table-striped"},[_c('thead',[_c('tr',[_c('th',[_vm._v("STR")]),_c('th',[_vm._v("DEX")]),_c('th',[_vm._v("CON")]),_c('th',[_vm._v("INT")]),_c('th',[_vm._v("WIS")]),_c('th',[_vm._v("CHA")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("8")]),_c('td',[_vm._v("14")]),_c('td',[_vm._v("16")]),_c('td',[_vm._v("10")]),_c('td',[_vm._v("8")]),_c('td',[_vm._v("18")])])])]),_c('h4',[_vm._v("Backstory")]),_c('p',[_vm._v("\n        I was born Martinus Cato to factory workers in Desadorel. My parents died in a fire when the factory they worked in burned down. The owner had locked all the doors to keep people working. No one escaped. I was 8 years old at the time, my little sister was 5. \n      ")]),_c('p',[_vm._v("\n        We were dumped at an orphanage, where the woman in charge forced us to work sewing clothes 14 hours a day. We lived with that for a month before I woke my sister up in the middle of the night and we climbed out the window. We ran back to our old apartment building, but our door was locked. Old Krozuth, the drunk dwarf who lived next door was around though and took us in. We lived with him for 2 years. He wasn't much of a parent, but he made sure we were fed. He couldn't hold down a job though and got evicted. Krozuth took to begging, \n      ")]),_c('p',[_vm._v("\n        My sister and I, now 10 and 7, took to pick-pocketing and burglary to survive. Pretty soon I got noticed, by the Black Suns. A local boss by the name of Galan Yeslar found us, offered us a job working for him. We give him a cut of our take, he gives us a roof to sleep under and food to eat. Fair enough right? We made a point to give some of what we stole to Krozuth too, the guy wasn't getting any younger, and he was dear to us. Galan put us up in a boarding house, claimed we were his cousin's kids or something. Anyway, the woman who ran the place, Amelia Veneziale, took care of us reasonably well, and the front was that any money we had was given to us by Galan. \n      ")]),_c('p',[_vm._v("\n        Working in the Black Suns I never really did better than a street-level pickpocket or small-time thief. The one time I was offered a better job I got paired with this nasty fellow by the name of Malar Bloodtooth. We were supposed to hold up some rich guy on the street. He resisted, I didn't want to fight. Malar cut him down without a thought. I ran. Never got offered any better work after that.\n      ")]),_c('p',[_vm._v("\n        I died suddenly at the age of 19 when I was doing some second-story work (or more accurately 10th story work) and bet my partner, Curia Luti, that I could make the jump from the roof of one building to the next. I'd always had a crush on Curia, and we'd been partners for a couple of years now. Making a prayer to whatever God would hear me, I attempted the jump and fell to my death. A stupid attempt to impress a girl. My last thoughts were of my sister. Who was going to take care of her now?\n      ")]),_c('p',[_vm._v("\n        With the Gods unable to decide on the rightful place for my soul, I found myself in Limbo, lost and wandering. Limbo is, not fun. There is no order there, so my mind can't make sense of anything. Everything is just an endless haze\n      ")]),_c('p',[_vm._v("\n        The fog of limbo lifts. Who knows how long I have wandered the endless plane of Limbo, my mind unable to focus properly. Though the cleared consciousness is great, I stand before the iconic image of death himself. The angel Azrael, cloaked black holding a scythe. His hood up. Only a void where his face should be. His voice is deep and slow. \"Martinus Cato?\" It asks. I attempt to speak but find I can't, so I only nod. \"You have been chosen for a task most grave. An ancient relic requires your protection and understanding.\" I attempt to ask questions, but I still can't speak. \"You are hereby offered a second chance at life, complete the task, and the life is yours for as long as it lasts,\" I note that he does not describe what happens if I fail. \"Do you accept the terms?\" I again try to ask questions, to find out anything else, but I still can't speak, and Death's face holds no emotions. Returning to that endless fog is not an option. I nod my head in agreement.\n      ")]),_c('p',[_vm._v("\n        A black portal manifests behind Azrael. \"Then step forth. Your task is to understand the relic and destroy all that would claim it from you. Follow the relic, it knows the way, but be wary of what it offers.\" With that, Death dissipates into nothing, leaving behind the portal. The fog of limbo begins closing in once again, so I make my way through. \n      ")]),_c('p',[_vm._v("\n        Inside, unexpectedly, is a very fancy hallway. Complete with trimmed wooden lining, a beautiful blue carpet, and some paintings along the wall. I attempt to make them out, but they blur as I look closer. Down the hallway is a wooden door, with a golden sign on it, in a language I do not recognize. I turn around and see only darkness the other way. I approach the door, but it is locked. I hear a voice on the other side. I lean in and listen close. It says only a singular word in an unfamiliar voice. \"Learn\". My vision fades in a flash of bright light, a black blob in front of me. Like a Rorschach blob. For a moment, I think it looks like a battle-ax, and at that moment, it becomes one, I reach out and take it. Intricately carved runes cover the black blade and handle. Holding it, it feels right, like it is a part of me.\n      ")]),_c('p',[_vm._v("\n        I wake up, pain spread across my body. I feel hunger and thirst like I have never felt before. My body is broken, literally, at least my wrist, and a leg may be fractured. Wherever I am, it is dark, and it reeks of garbage. I attempt to move, but I must have several fractures throughout my body, as my bones are stiff in place. I can barely breathe, my body is a mess, and most notable, there is a throbbing in the back of my skull where I can feel the congealed blood. The gods have played a cruel joke and given me a dead man's body.\n      ")]),_c('p',[_vm._v("\n        But then, the area I am in lights up. My body begins to glow faintly, and all the wounds close in unison. My vision focuses, and I can see. I am still hungry and thirsty, but not to the point of death. With the clearing of the pain comes a clearing of my mind, and in it are the last thoughts of a dying man \"Meet Bern at the Shark's Tail inn\". I move and notice something in my grip, it's the battle-ax from my vision. I move to remove myself from wherever I am.  Fuck, I think, not exactly the second chance at life I thought this would be. Grabbing the ax by the handle I limp away from the scene. \n      ")]),_c('p',[_vm._v("\n        I am in some alley. It is nearing dark. I think to myself \"What have you gotten yourself into Martinus?\", but the name feels wrong. I think to myself a moment about my name. Agriel Osiris comes to mind. It feels right. Was it this person's name? I cannot help but think of myself as Agriel. I remember the dying man's last thoughts \"Meet Bern at the Shark's Tail Inn\". It seems to be all I have to go on for now.\n      ")]),_c('p',[_vm._v("\n        Walking out into the main street I can't help but notice the stares. A small child watches me for a brief minute before running into her house. \"Shit, I need a mirror, what do I even look like?\"\n      ")]),_c('p',[_vm._v("\n        A voice comes from a nearby doorway. \"Come inside, blessed one, away from the stares of the unbelievers.\" A woman is standing there, in a black silk dress that accentuates her curves, she is older than me but still quite beautiful. Warily, I follow her inside. She tells me her name is Rachele and she shows me my appearance in a mirror. My skin, so pale it glows. My irises are white flaked with black. The effect is, otherworldly. I can feel a strange energy inside, waiting to be released. Rachele worships Nera in secret even providing rites of death for the nearby villagers. What the hell have I gotten myself into? \"Go to the Shark's Tail Inn, find Bern.\" She tells me.\n      ")]),_c('p',[_vm._v("\n        \"Who the hell is Bern?\" I ask.\n      ")]),_c('p',[_vm._v("\n        \"I do not know. Go, it is your task.\" Some task, but at least it's something. Maybe I can find out where the hell I am from Bern.\n      ")]),_c('h3',[_vm._v("Xane Ludor")]),_c('p',[_c('strong',[_vm._v("Race:")]),_vm._v(" Altasoror Human")]),_c('p',[_c('strong',[_vm._v("Class/Level:")]),_vm._v(" Rogue 1 / Knowledge Cleric 4")]),_c('table',{staticClass:"table table-striped"},[_c('thead',[_c('tr',[_c('th',[_vm._v("STR")]),_c('th',[_vm._v("DEX")]),_c('th',[_vm._v("CON")]),_c('th',[_vm._v("INT")]),_c('th',[_vm._v("WIS")]),_c('th',[_vm._v("CHA")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("?")]),_c('td',[_vm._v("14")]),_c('td',[_vm._v("10")]),_c('td',[_vm._v("20")]),_c('td',[_vm._v("?")]),_c('td',[_vm._v("?")])])])]),_c('h3',[_vm._v("Paradan")]),_c('p',[_c('strong',[_vm._v("Race:")]),_vm._v(" (Wood?) Elf")]),_c('p',[_c('strong',[_vm._v("Class/Level:")]),_vm._v(" (Way of the Open Hand?) Monk 5")]),_c('table',{staticClass:"table table-striped"},[_c('thead',[_c('tr',[_c('th',[_vm._v("STR")]),_c('th',[_vm._v("DEX")]),_c('th',[_vm._v("CON")]),_c('th',[_vm._v("INT")]),_c('th',[_vm._v("WIS")]),_c('th',[_vm._v("CHA")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("?")]),_c('td',[_vm._v("20")]),_c('td',[_vm._v("?")]),_c('td',[_vm._v("?")]),_c('td',[_vm._v("?")]),_c('td',[_vm._v("?")])])])])]),_c('b-tab',{attrs:{"title":"Resources"}},[_c('a',{attrs:{"href":"https://docs.google.com/document/d/1diJ_vKN03Esbddhd3ytSXHxNY-IBZqMhrv4-38MC-rU/edit"}},[_vm._v("Beladonis Magical Wonders")])])],1)],1)}
